import Pulsator from "../src/pulsator";
var options = {
    style: {
        width: "20px",
        height: "20px",
    },
    color: {
        r: 244,
        g: 67,
        b: 54
    },
    duration: 1200,
    iterations: 10,
};
var parent = document.querySelector(".demo-space");
var pulsator = new Pulsator(options);
var el = pulsator.getElement();
el.style.position = "absolute";
if (parent === null) {
    throw new Error('Parent node is null');
}
parent.appendChild(el);
parent.addEventListener('click', function (e) {
    if (e instanceof MouseEvent) {
        pulsator.startByMouseEvent(e);
    }
});
el.addEventListener('mouseover', function () { return pulsator.pause(); });
el.addEventListener('mouseleave', function () { return pulsator.start(); });
window.addEventListener('keydown', function (e) {
    switch (e.key) {
        case 'Escape':
            pulsator.stop();
            break;
        case 'Enter':
            pulsator.reverse();
            break;
    }
});
